      >
        <h4>Définir les seuils d'expédition au coût par ordre croissant</h4>
        <div
          v-for="(threshold, index) in opts.shipping_thresholds"
          :key="index"
        >
          <div class="threshold">
            <input type="text" v-model="threshold[0]" />€ jusqu'à
            <input type="text" v-model="threshold[1]" />€ de commande
            <button class="delete" @click="remove_threshold(index)">x</button>
          </div>
        </div>
        <button class="button" @click="opts.shipping_thresholds.push([0, 0])">
          Ajouter un seuil
        </button>
      </div>

      <div
        :style="{ opacity: !opts.fixed_shipping ? 1 : 0.5 }"
        style="margin-bottom: 1em"
      >
        <h4>Définir les seuils d'expédition au poids par ordre croissant</h4>
        <div
          v-for="(threshold, index) in opts.shipping_thresholds_w"
