        >
          <div class="threshold">
            <input type="text" v-model="threshold[0]" />€ jusqu'à
            <input type="text" v-model="threshold[1]" />grammes de poids
            <button class="delete" @click="remove_threshold_w(index)">x</button>
          </div>
        </div>
        <button class="button" @click="opts.shipping_thresholds_w.push([0, 0])">
          Ajouter un seuil
        </button>
      </div>

      <div style="margin-bottom: 1em">
        <h4>Définir les codes promo actifs</h4>
        <div
          class="promcode"
          v-for="(code, index) in opts.prom_codes"
          :key="index"
        >
          <label>Nom <input type="text" v-model="code[0]" /> </label>
          <label
            >Type <select name="" id="" v-model="code[1]">
              <option value="fixed">Remise fixe</option>
              <option value="ratio">Remise en %</option>
            </select>
            </label
          >
          <label>Valeur <input type="number" v-model="code[2]" /> </label>
          <button class="delete" @click="remove_prom_code(index)">x</button>
        </div>
        <button class="button" @click="opts.prom_codes.push(['', 'fixed', 2])">
          Ajouter un code promo
        </button>
      </div>
      <label for="">
        <input type="checkbox" v-model="opts.limit_europa" /> La commande est
        limitée à l'Europe
      </label>
      <label for="">
        <input type="checkbox" v-model="opts.has_local" /> Le retrait en magasin
        est disponible
      </label>
      <label for="">
        <input type="checkbox" v-model="opts.sub_free" /> Les abonnements
        n'occasionnent pas de frais de port supplémentaires
      </label>
    </div>
    <div class="basket">
      <h3>Mon panier</h3>
      <div class="items">
        <div class="item" v-for="(item, index) in basket" :key="index">
          <div v-if="item.type === 'book'">
            <h4>
              Livre (<input
                type="number"
                class="inline"
                v-model="item.price"
              />€, pèse
              <input type="number" class="inline" v-model="item.weight" />g)
            </h4>
            <label for="">
              Quantité :
              <input type="number" min="1" step="1" v-model="item.qty" />
            </label>
          </div>
          <div v-if="item.type === 'abo'">
            <h4>
              Abonnement (<input
                type="number"
                class="inline"
                v-model="item.price"
              />€ / mois, poids d'un envoi
              <input type="number" class="inline" v-model="item.weight" />g)
            </h4>
            <label for="">
              Durée de l'abonnement :
              <input
                type="number"
                min="3"
                max="24"
                step="3"
                v-model="item.duration"
              />
              mois
            </label>
          </div>
          <div v-if="item.type === 'object'">
            <h4>
              Objet (<input
                type="number"
                class="inline"
                v-model="item.price"
              />€, pèse
              <input type="number" class="inline" v-model="item.weight" />g)
            </h4>
            <label for="">
              Quantité :
              <input type="number" min="1" step="1" v-model="item.qty" />
            </label>
          </div>
          <button class="delete" @click="remove_item(index)">x</button>
        </div>
      </div>
      <div class="buttons">
        <button
          @click="basket.push({ type: 'book', price: 10, qty: 1, weight: 750 })"
        >
          Ajouter livre</button
        >&nbsp;
        <button
          @click="
            basket.push({
              type: 'abo',
              price: 25,
              qty: 1,
              duration: 6,
              price: 10,
              weight: 1000,
            })
          "
        >
          Ajouter abonnement</button
        >&nbsp;
        <button
          @click="
            basket.push({ type: 'object', price: 30, qty: 1, weight: 750 })
          "
        >
          Ajouter objet
        </button>
      </div>
      <hr />
      <div class="total" v-if="has_items">
        <h3>Total sans promo : {{ total }}€ / avec promo: {{ total_prom }}€</h3>
        <label for=""
          >J'ai un code promo ! <input type="text" v-model="prom_code"
        /></label>
        <div v-if="prom_code">
            <div v-if="valid_prom">Code promo bien pris en compte</div>
            <div v-else>Code promo expiré ou invalide :(</div>
          </div>
        <hr />
        <h3>Coordonnées</h3>
        <div>
          <label for=""
            ><input type="checkbox" v-model="has_delivery" />Mon adresse de
            livraison est différente de mon adresse de facturation</label
          >
        </div>
        <h4>
          Adresse de facturation
          <span v-if="!has_delivery">et de livraison</span>
        </h4>
        <label
          ><select v-model="coords_billing.zone">
            <option value="Europe">Europe</option>
            <option value="OutsideEU">Hors Europe</option>
          </select>
          Zone géographique</label
        >
        <label
          ><input type="text" v-model="coords_billing.last_name" /> Nom</label
        >
        <label
          ><input
            type="text"
            v-model="coords_billing.first_name"
          />Prénom</label
        >
        <label
          ><input type="text" v-model="coords_billing.email" />E-mail</label
        >
        <label
          ><input type="text" v-model="coords_billing.email_2" />Confirmer
          l'e-mail</label
        >
        <label
          ><input type="text" v-model="coords_billing.address" />Adresse</label
        >
        <label
          ><input type="text" v-model="coords_billing.address_comp" />Complément
          d'adresse</label
        >
        <label
          ><input type="text" v-model="coords_billing.post_code" />Code
          postal</label
        >
        <label><input type="text" v-model="coords_billing.city" />Ville</label>
        <label
          ><input type="text" v-model="coords_billing.country" />Pays</label
        >
        <div v-if="has_delivery">
          <h4>Adresse de livraison</h4>
          <label
            ><select v-model="coords_delivery.zone">
              <option value="Europe">Europe</option>
              <option value="OutsideEU">Hors Europe</option>
            </select>
            Zone géographique</label
          >
          <label
            ><input type="text" v-model="coords_delivery.last_name" />
            Nom</label
          >
          <label
            ><input
              type="text"
              v-model="coords_delivery.first_name"
            />Prénom</label
          >
          <label
            ><input type="text" v-model="coords_delivery.phone" />Numéro de
            téléphone</label
          >
          <label
            ><input
              type="text"
              v-model="coords_delivery.address"
            />Adresse</label
          >
          <label
            ><input
              type="text"
              v-model="coords_delivery.address_comp"
            />Complément d'adresse</label
          >
          <label
            ><input type="text" v-model="coords_delivery.post_code" />Code
            postal</label
          >
          <label
            ><input type="text" v-model="coords_delivery.city" />Ville</label
          >
          <label
            ><input type="text" v-model="coords_delivery.country" />Pays</label
          >
        </div>
        <hr />
        <div v-if="can_ship">
          <h3>
            Méthode d'expédition (<span v-if="opts.fixed_shipping"
              >Coût au montant</span
            >
            <span v-else>Coût au poids : poids considéré {{ weight }}g</span>)
          </h3>
          <div>
            <label for="" v-if="opts.has_local"
              ><input
                type="radio"
                name="shipping"
                v-model="opts.shipping"
                value="local"
              />
              Retrait au local 0€</label
            >
            <label for=""
              ><input
                type="radio"
                name="shipping"
                v-model="opts.shipping"
                value="relay"
              />
              Point Mondial Relay {{ relay_shipping }}€</label
            >
            <label for=""
              ><input
                type="radio"
                name="shipping"
                v-model="opts.shipping"
                value="standard"
              />
              Colissimo à domicile {{ colissimo_shipping }}€</label
            >
          </div>
          <div v-if="opts.shipping !== 'local'">
            <label for=""
              ><input type="checkbox" v-model="opts.shipping_boomerang" />Colis
              Boomerang (+2€)</label
            >
          </div>
          <h3>Total frais de port compris : {{ total_prom + shipping }}€</h3>
          
        </div>
        <div v-else>
          Nous ne livrons pas encore en dehors de l'Europe. Vous pouvez nous
          contacter directement pour discuter d'une possible commande hors
          Europe.
        </div>
      </div>
      <hr>
      <div>
        <div v-if="can_ship && opts.shipping === 'local'">
          <h4>Retrait local - après paiement</h4>
          <ul>
            <li>Flora est informée de la nouvelle commande</li>
            <li>L'acheteur(euse) reçoit sa facture</li>
            <li>Il faut convenir d'un créneau de rendez-vous</li>
          </ul>
        </div> 
        <div v-if="can_ship && opts.shipping !== 'local'">
            <h4>Envoi postal - avant paiement</h4>
            <ul>
              <li>Il faut choisir un point relais dans le cas de mondial relay</li>
              <li>Quel fonctionnement si c'est un abonnement ?</li>
            </ul>
            <h4>Envoi postal - après paiement</h4>
            <ul>
              <li>Flora est informée de la nouvelle commande</li>
              <li>L'acheteur(euse) reçoit sa facture</li>
              <li>Il faut fournir à l'acheteur(euse) son numéro de suivi</li>
            </ul>
        </div> 
      </div>
    </div>
  </div>
</template>

<script>
const find_slot = (slots, amount) => {
  // eslint-disable-next-line
  return slots.find(([a, b]) => amount < b)[0];
};

const n = a => Number(a.toString());

export default {
  name: "App",
  data() {
    return {
      opts: {
        fixed_shipping: false,
        limit_europa: false,
        has_local: false,
        sub_free: false,
        shipping_thresholds: [
          [5, 25],
          [3, 50],
          [0, 99999],
        ],
        shipping_thresholds_w: [
          [5, 1000],
          [10, 3000],
          [15, 99999],
        ],
        prom_codes: [
          ["student", "ratio", 5],
          ["summer", "fixed", 5],
        ],
        shipping_boomerang: false,
        shipping: "standard",
      },
      basket: [
        { type: "book", price: 25, qty: 1, weight: 750 },
        { type: "book", price: 10, qty: 1, weight: 750 },
        { type: "abo", qty: 1, duration: 6, price: 10, weight: 1000 },
        { type: "object", price: 30, qty: 1, weight: 750 },
      ],
      has_delivery: false,
      prom_code: "",
      coords_billing: {
        zone: "Europe",
        country: "France",
        first_name: "",
        last_name: "",
        email: "",
        email_2: "",
        address: "",
        address_comp: "",
        post_code: "",
        city: "",
      },
      coords_delivery: {
        zone: "Europe",
        country: "France",
        first_name: "",
        last_name: "",
        phone: "",
        address: "",
        address_comp: "",
        post_code: "",
        city: "",
      },
    };
  },
  computed: {
    has_items() {
      return this.basket.length > 0;
    },
    can_ship() {
      return !this.opts.limit_europa || (this.has_delivery
        ? this.coords_delivery.zone === "Europe"
        : this.coords_billing.zone === "Europe");
    },
    total() {
      return this.basket.reduce((out, item) => {
        let cost = n(item.qty) * n(item.price);
        if (item.type === "abo") {
          cost = cost * n(item.duration);
        }
        return out + cost;
      }, 0);
    },
    total_prom() {
      if (!this.valid_prom) return this.total;
      if (this.valid_prom[1] === "ratio") {
        return (this.total * (1 - n(this.valid_prom[2]) / 100)).toFixed(2);
      } else {
        return this.total - n(this.valid_prom[2]);
      }
    },
    weight() {
      return this.basket.reduce((out, item) => {
        let weight = n(item.weight) * n(item.qty);
        if (item.type === "abo" && !this.opts.sub_free) {
          weight = weight * n(item.duration);
        } else if (item.type === "abo" && this.opts.sub_free) {
          weight = 0;
        }
        return out + weight;
      }, 0);
    },
    shipping() {
      if (this.opts.shipping === "local") return 0;
      if (this.opts.shipping === "standard")
        return this.colissimo_shipping + (this.opts.shipping_boomerang ? 2 : 0);
      if (this.opts.shipping === "relay")
        return this.relay_shipping + (this.opts.shipping_boomerang ? 2 : 0);
      return 0;
    },
    relay_shipping() {
      if (this.opts.fixed_shipping) {
        return find_slot(this.opts.shipping_thresholds, this.total);
      } else {
        return find_slot(this.opts.shipping_thresholds_w, this.weight);
      }
    },
    colissimo_shipping() {
      if (this.opts.fixed_shipping) {
        return find_slot(this.opts.shipping_thresholds, this.total);
      } else {
        return find_slot(this.opts.shipping_thresholds_w, this.weight);
      }
    },
    valid_prom() {
      // eslint-disable-next-line
      let c = this.opts.prom_codes.find(([a, b, c]) => a === this.prom_code);
      return c || false;
    }
  },
  components: {},
  methods: {
    remove_threshold(index) {
      this.opts.shipping_thresholds = this.opts.shipping_thresholds.filter(
        (a, i) => i !== index
      );
    },
    remove_threshold_w(index) {
      this.opts.shipping_thresholds_w = this.opts.shipping_thresholds_w.filter(
        (a, i) => i !== index
      );
    },
    remove_prom_code(index) {
      this.opts.prom_codes = this.opts.prom_codes.filter((a, i) => i !== index);
    },
    remove_item(index) {
      this.basket = this.basket.filter((a, i) => i !== index);
    },
  },
};
</script>

